	module.exports = {"pathContext":{"docHtml":"<h2><strong>6.4.0</strong></h2>\n<ul>\n<li>[New] <code>qs.stringify</code>: add <code>encodeValuesOnly</code> option</li>\n<li>[Fix] follow <code>allowPrototypes</code> option during merge (#201, #201)</li>\n<li>[Fix] support keys starting with brackets (#202, #200)</li>\n<li>[Fix] chmod a-x</li>\n<li>[Dev Deps] update <code>eslint</code></li>\n<li>[Tests] up to <code>node</code> <code>v7.7</code>, <code>v6.10</code>,<code>v4.8</code>; disable osx builds since they block linux builds</li>\n<li>[eslint] reduce warnings</li>\n</ul>\n<h2><strong>6.3.1</strong></h2>\n<ul>\n<li>[Fix] ensure that <code>allowPrototypes: false</code> does not ever shadow Object.prototype properties (thanks, @snyk!)</li>\n<li>[Dev Deps] update <code>eslint</code>, <code>@ljharb/eslint-config</code>, <code>browserify</code>, <code>iconv-lite</code>, <code>qs-iconv</code>, <code>tape</code></li>\n<li>[Tests] on all node minors; improve test matrix</li>\n<li>[Docs] document stringify option <code>allowDots</code> (#195)</li>\n<li>[Docs] add empty object and array values example (#195)</li>\n<li>[Docs] Fix minor inconsistency/typo (#192)</li>\n<li>[Docs] document stringify option <code>sort</code> (#191)</li>\n<li>[Refactor] <code>stringify</code>: throw faster with an invalid encoder</li>\n<li>[Refactor] remove unnecessary escapes (#184)</li>\n<li>Remove contributing.md, since <code>qs</code> is no longer part of <code>hapi</code> (#183)</li>\n</ul>\n<h2><strong>6.3.0</strong></h2>\n<ul>\n<li>[New] Add support for RFC 1738 (#174, #173)</li>\n<li>[New] <code>stringify</code>: Add <code>serializeDate</code> option to customize Date serialization (#159)</li>\n<li>[Fix] ensure <code>utils.merge</code> handles merging two arrays</li>\n<li>[Refactor] only constructors should be capitalized</li>\n<li>[Refactor] capitalized var names are for constructors only</li>\n<li>[Refactor] avoid using a sparse array</li>\n<li>[Robustness] <code>formats</code>: cache <code>String#replace</code></li>\n<li>[Dev Deps] update <code>browserify</code>, <code>eslint</code>, <code>@ljharb/eslint-config</code>; add <code>safe-publish-latest</code></li>\n<li>[Tests] up to <code>node</code> <code>v6.8</code>, <code>v4.6</code>; improve test matrix</li>\n<li>[Tests] flesh out arrayLimit/arrayFormat tests (#107)</li>\n<li>[Tests] skip Object.create tests when null objects are not available</li>\n<li>[Tests] Turn on eslint for test files (#175)</li>\n</ul>\n<h2><strong>6.2.2</strong></h2>\n<ul>\n<li>[Fix] ensure that <code>allowPrototypes: false</code> does not ever shadow Object.prototype properties</li>\n</ul>\n<h2><strong>6.2.1</strong></h2>\n<ul>\n<li>[Fix] ensure <code>key[]=x&#x26;key[]&#x26;key[]=y</code> results in 3, not 2, values</li>\n<li>[Refactor] Be explicit and use <code>Object.prototype.hasOwnProperty.call</code></li>\n<li>[Tests] remove <code>parallelshell</code> since it does not reliably report failures</li>\n<li>[Tests] up to <code>node</code> <code>v6.3</code>, <code>v5.12</code></li>\n<li>[Dev Deps] update <code>tape</code>, <code>eslint</code>, <code>@ljharb/eslint-config</code>, <code>qs-iconv</code></li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=36&#x26;state=closed\"><strong>6.2.0</strong></a></h2>\n<ul>\n<li>[New] pass Buffers to the encoder/decoder directly (#161)</li>\n<li>[New] add \"encoder\" and \"decoder\" options, for custom param encoding/decoding (#160)</li>\n<li>[Fix] fix compacting of nested sparse arrays (#150)</li>\n</ul>\n<h2><strong>6.1.1</strong></h2>\n<ul>\n<li>[Fix] ensure that <code>allowPrototypes: false</code> does not ever shadow Object.prototype properties</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=35&#x26;state=closed\"><strong>6.1.0</strong></a></h2>\n<ul>\n<li>[New] allowDots option for <code>stringify</code> (#151)</li>\n<li>[Fix] \"sort\" option should work at a depth of 3 or more (#151)</li>\n<li>[Fix] Restore <code>dist</code> directory; will be removed in v7 (#148)</li>\n</ul>\n<h2><strong>6.0.3</strong></h2>\n<ul>\n<li>[Fix] ensure that <code>allowPrototypes: false</code> does not ever shadow Object.prototype properties</li>\n<li>[Fix] Restore <code>dist</code> directory; will be removed in v7 (#148)</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=33&#x26;state=closed\"><strong>6.0.2</strong></a></h2>\n<ul>\n<li>Revert ES6 requirement and restore support for node down to v0.8.</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=32&#x26;state=closed\"><strong>6.0.1</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/pull/127\"><strong>#127</strong></a> Fix engines definition in package.json</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=31&#x26;state=closed\"><strong>6.0.0</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/124\"><strong>#124</strong></a> Use ES6 and drop support for node &#x3C; v4</li>\n</ul>\n<h2><strong>5.2.1</strong></h2>\n<ul>\n<li>[Fix] ensure <code>key[]=x&#x26;key[]&#x26;key[]=y</code> results in 3, not 2, values</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=30&#x26;state=closed\"><strong>5.2.0</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/64\"><strong>#64</strong></a> Add option to sort object keys in the query string</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=29&#x26;state=closed\"><strong>5.1.0</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/117\"><strong>#117</strong></a> make URI encoding stringified results optional</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/106\"><strong>#106</strong></a> Add flag <code>skipNulls</code> to optionally skip null values in stringify</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=28&#x26;state=closed\"><strong>5.0.0</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/114\"><strong>#114</strong></a> default allowDots to false</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/100\"><strong>#100</strong></a> include dist to npm</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=26&#x26;state=closed\"><strong>4.0.0</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/98\"><strong>#98</strong></a> make returning plain objects and allowing prototype overwriting properties optional</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=24&#x26;state=closed\"><strong>3.1.0</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/89\"><strong>#89</strong></a> Add option to disable \"Transform dot notation to bracket notation\"</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=23&#x26;state=closed\"><strong>3.0.0</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/80\"><strong>#80</strong></a> qs.parse silently drops properties</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/77\"><strong>#77</strong></a> Perf boost</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/60\"><strong>#60</strong></a> Add explicit option to disable array parsing</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/74\"><strong>#74</strong></a> Bad parse when turning array into object</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/81\"><strong>#81</strong></a> Add a <code>filter</code> option</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/68\"><strong>#68</strong></a> Fixed issue with recursion and passing strings into objects.</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/66\"><strong>#66</strong></a> Add mixed array and object dot notation support Closes: #47</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/76\"><strong>#76</strong></a> RFC 3986</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/85\"><strong>#85</strong></a> No equal sign</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/84\"><strong>#84</strong></a> update license attribute</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=20&#x26;state=closed\"><strong>2.4.1</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/73\"><strong>#73</strong></a> Property 'hasOwnProperty' of object #<Object> is not a function</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=19&#x26;state=closed\"><strong>2.4.0</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/70\"><strong>#70</strong></a> Add arrayFormat option</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=18&#x26;state=closed\"><strong>2.3.3</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/59\"><strong>#59</strong></a> make sure array indexes are >= 0, closes #57</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/58\"><strong>#58</strong></a> make qs usable for browser loader</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=17&#x26;state=closed\"><strong>2.3.2</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/55\"><strong>#55</strong></a> allow merging a string into an object</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=16&#x26;state=closed\"><strong>2.3.1</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/52\"><strong>#52</strong></a> Return \"undefined\" and \"false\" instead of throwing \"TypeError\".</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=15&#x26;state=closed\"><strong>2.3.0</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/50\"><strong>#50</strong></a> add option to omit array indices, closes #46</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=14&#x26;state=closed\"><strong>2.2.5</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/39\"><strong>#39</strong></a> Is there an alternative to Buffer.isBuffer?</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/49\"><strong>#49</strong></a> refactor utils.merge, fixes #45</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/41\"><strong>#41</strong></a> avoid browserifying Buffer, for #39</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=13&#x26;state=closed\"><strong>2.2.4</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/38\"><strong>#38</strong></a> how to handle object keys beginning with a number</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=12&#x26;state=closed\"><strong>2.2.3</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/37\"><strong>#37</strong></a> parser discards first empty value in array</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/36\"><strong>#36</strong></a> Update to lab 4.x</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=11&#x26;state=closed\"><strong>2.2.2</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/33\"><strong>#33</strong></a> Error when plain object in a value</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/34\"><strong>#34</strong></a> use Object.prototype.hasOwnProperty.call instead of obj.hasOwnProperty</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/24\"><strong>#24</strong></a> Changelog? Semver?</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=10&#x26;state=closed\"><strong>2.2.1</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/32\"><strong>#32</strong></a> account for circular references properly, closes #31</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/31\"><strong>#31</strong></a> qs.parse stackoverflow on circular objects</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=9&#x26;state=closed\"><strong>2.2.0</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/26\"><strong>#26</strong></a> Don't use Buffer global if it's not present</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/30\"><strong>#30</strong></a> Bug when merging non-object values into arrays</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/29\"><strong>#29</strong></a> Don't call Utils.clone at the top of Utils.merge</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/23\"><strong>#23</strong></a> Ability to not limit parameters?</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=8&#x26;state=closed\"><strong>2.1.0</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/22\"><strong>#22</strong></a> Enable using a RegExp as delimiter</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=7&#x26;state=closed\"><strong>2.0.0</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/18\"><strong>#18</strong></a> Why is there arrayLimit?</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/20\"><strong>#20</strong></a> Configurable parametersLimit</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/21\"><strong>#21</strong></a> make all limits optional, for #18, for #20</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=6&#x26;state=closed\"><strong>1.2.2</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/19\"><strong>#19</strong></a> Don't overwrite null values</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=5&#x26;state=closed\"><strong>1.2.1</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/16\"><strong>#16</strong></a> ignore non-string delimiters</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/15\"><strong>#15</strong></a> Close code block</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=4&#x26;state=closed\"><strong>1.2.0</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/12\"><strong>#12</strong></a> Add optional delim argument</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/13\"><strong>#13</strong></a> fix #11: flattened keys in array are now correctly parsed</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=3&#x26;state=closed\"><strong>1.1.0</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/7\"><strong>#7</strong></a> Empty values of a POST array disappear after being submitted</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/9\"><strong>#9</strong></a> Should not omit equals signs (=) when value is null</li>\n<li><a href=\"https://github.com/ljharb/qs/issues/6\"><strong>#6</strong></a> Minor grammar fix in README</li>\n</ul>\n<h2><a href=\"https://github.com/ljharb/qs/issues?milestone=2&#x26;state=closed\"><strong>1.0.2</strong></a></h2>\n<ul>\n<li><a href=\"https://github.com/ljharb/qs/issues/5\"><strong>#5</strong></a> array holes incorrectly copied into object on large index</li>\n</ul>","docPath":"argo-ci/node_modules/fsevents/node_modules/qs/changelog","proj":"argo-ci"}}

